const template = `
<div class="graph-panel" ng-class="{'graph-panel--legend-right': ctrl.panel.legend.rightSide}">
  <div class="graph-panel__chart" grafana-graph ng-dblclick="ctrl.zoomOut()">
  </div>

  <div class="graph-legend">
    <div class="graph-legend-content" graph-legend></div>
  </div>
  <div class="btn col-6 btn-success" ng-class="{'disabled' : this.timeSrv.refresh == undefined, '' : this.timeSrv.refresh != undefined}" ng-click="ctrl.toggleRefresh(true)">Start</div>
  <div class="btn col-6 btn-warning" ng-click="ctrl.toggleRefresh(false)">Pause</div>
</div>
`;

export default template;
